import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import styles from "./Join.module.css";

const Join = ({ authService }) => {
	const history = useHistory();
	const goToHome = () => {
		history.push("/");
	};
	const [joinUser, setJoinUser] = useState({
		joinID: "",
		joinPW: "",
	});

	const [alertMsg, setAlertMsg] = useState("");

	const onChange = (e) => {
		setJoinUser({
			...joinUser,
			[e.target.name]: e.target.value,
		});
	};

	const onSubmit = (e) => {
		e.preventDefault();
		authService
			.newJoin(joinUser.joinID, joinUser.joinPW)
			.then((data) => {
				alert("Success full");
			})
			.catch((error) => {
				setAlertMsg(error.message);
			});
	};

	return (
		<>
			<div className={styles.joinWrap}>
				<h1 className={styles.title}>íšŒì›ê°€ì…</h1>
				<button type="button" className={styles.home} onClick={goToHome}>
					í™ˆìœ¼ë¡œ
				</button>
				<form onSubmit={onSubmit}>
					<div className={styles.join_input}>
						<input placeholder="EMAIL" type="text" name="joinID" onChange={onChange} />
					</div>
					<div className={styles.join_input}>
						<input placeholder="PASSWORD" type="password" name="joinPW" onChange={onChange} />
					</div>
					<div className={styles.alertMessageError}>{alertMsg}</div>
					<button type="submit" className={styles.submit}>
						íšŒì›ê°€ì…
					</button>
				</form>
			</div>
		</>
	);
};

export default Join;
